<template>
	<section>
		 <div class="inner clearfix">
       <div class="zx-hyzl-dl zx-myhr-zz-r">
          <h1><a href="javascript:;" :title="info.title">{{info.title | truncate(30)}}</a></h1>
          <div>
            <img :src="info.columnCover" alt="">{{info.columnTitle}}<em class="zx-pop-zl"  v-model="info.subStatus" @click="subscription(info)"  style="cursor: pointer;">{{info.subStatus==1?'已订阅':'订阅专栏'}}</em>   
            <span>
              <i class="zx-icon zx-ico-yl"></i>{{info.browseCount||0}}
              <i class="zx-icon zx-ico-sc1"></i>{{info.collectionCount||0}}
              <i class="zx-icon zx-ico-dz"></i>{{info.fabulousCount||0}}
            </span>
          </div>
          <p>{{info.tex}}</p>
          <div class="zx-pop-art">
           	 分享：	
           	<div class="bdsharebuttonbox" data-tag = "share_0">
				<a href="#" class="bds_weixin" data-cmd="weixin" title="分享到微信"></a>
				<a href="#" class="bds_tsina" data-cmd="tsina" title="分享到新浪微博"></a>
			</div>
            <span>
              <a href="javascript:;" @click="likeFn(info)"><i :class="info.fabStatus==1?'zx-icon zx-ico-likh':'zx-icon zx-ico-lik'"  v-model="info.fabStatus"></i><em>{{info.fabulousCount||0}}</em></a>
              <a href="javascript:;" class="zx-pop-asc" @click="shoucanginfo(info)"><i :class="info.colStatus==1?'zx-icon zx-ico-sc1h':'zx-icon zx-ico-sc1'" v-model="info.colStatus"></i><em>{{info.collectionCount||0}}</em></a>
            </span>
          </div>
          <div class="zx-h-plt zx-hyzl-deh">
            <h1>发布评论</h1>
            <textarea class="zx-txt triangle-up" placeholder="欢迎留言交流......" v-model.trim="mgges"></textarea>
            <button type="button" class="zx-btn-pl" @click="infoCommentFn()">发布</button>
          </div>
          <ul class="zx-h-plcon clearfix">
              <li class="zx-myhr-zz-r zx-h-sc zx-h-pln" v-for="item in commonList">
                <div>
                  <img :src="item.userImg||'../../static/images/enter_bg1.jpg'" alt="" @click="getIndex(item.commentUserId)">{{item.userNickName||'昵称'}}  
                  <span>{{item.createDate |timestampFormat('YYYY-MM-DD HH:mm')}}<a href="javascript:;"></a></span>
                </div>
                <p>{{item.commentContent}}</p>
              </li>
          </ul>
          <div class="c-page zx-page" v-show="commonList.length">
                <el-pagination
	       			:page-size="pageSize"
			      	@current-change="pageFn"
				  	layout="prev, pager, next"
				  	:total=total>
				</el-pagination>
            </div>
       </div>
       <div class="zx-hyzl-dr" id="rightArticle">
         <h1>热门文章</h1>
         <ul class="zx-hyzl-cont">
           <li  v-for="(item,index) in articleList" @click="getArticle(item.uuid)"><em  :class="index<3?'zx-hytop':''">{{index+1}}</em><a href="javascript:;" :title="item.title">{{item.title | truncate(10)}}</a><span>{{item.browseCount}}</span></li>
         </ul>
       </div>
     </div>

	</section>
</template>

<script>
	import {articleDetails,hotArticle,commonList,infoComment,userLoad,likeReplay,dingYue,shoucang,share,cancle} from '../../api/info';
	export default{
		data(){
			return{
				
				currentpage:1,
				pageSize:4,
				pageNum:1,
				total:0,
				articleList:[],
				infoUuid:'',
				info:{},
				commonList:[],
				mgges:'',
				userid:'',
				art:{
					title:'',
					abstract:""
				}
			}
		},
		methods:{
			shoucanginfo(info){//收藏，取消收藏
				var param ={
					infoUUId:this.info.uuid,
					userId:this.userid
				}
				
				if(info.colStatus==0){
					info.colStatus=1
					info.collectionCount++
					shoucang(param).then(res=>{
						console.log(res)
					})
				}else if(info.colStatus==1){
					info.colStatus=0
					info.collectionCount--
					shoucang(param).then(res=>{
						console.log(res)
					})
				}
				
			},
			likeFn(info){//喜欢，取消喜欢
				var userData = JSON.parse(window.localStorage.getItem('user'))
	    		if(userData){
	    			var param ={
						objTpye:10,
						objId:this.info.infoId,
						fabulousUserId:this.userid
					}
					if(info.fabStatus==0){
						
						info.fabStatus=1
						info.fabulousCount++
						likeReplay(param).then(res=>{
							
						})
					}else if(info.fabStatus==1){
						info.fabStatus=0
						info.fabulousCount--
						likeReplay(param).then(res=>{
							
						})
					}
	    		}else{
	    			window.location.href=configURL.sys+'/login.htm?returnUrl='+configURL.infoUrl+'/page/#/ZLactive?uuid=' + this.uuid;
	    		} 
			},
			infoCommentFn(){//评论
				var userData = JSON.parse(window.localStorage.getItem('user'))
	    		if(userData){
	    			if(this.mgges!=''){
						var param={
							informationId:this.info.infoId,
							commentUserId:this.userid,
							commentContent:this.mgges
						}
						
						infoComment(param).then(res=>{
							this.commonListFn()
							this.mgges=''
						})
					}
	    		}else{
	    			window.location.href=configURL.sys+'/login.htm?returnUrl='+configURL.infoUrl+'/page/#/ZLactive?uuid=' + this.uuid;
	    		} 
			},
			subscription(item){//订阅，取消订阅
				var userData = JSON.parse(window.localStorage.getItem('user'))
	    		if(userData){
	    			if(item.subStatus==0){
						var param={
							columnId:item.id
						}
						item.subStatus=1
						dingYue(param).then(res=>{
		
						})
					}else if(item.subStatus==1){
						var param={
							columnId:item.id
						}
						item.subStatus=0
						cancle(param).then(res=>{
							
						})	
					}

	    		}else{
	    			window.location.href=configURL.sys+'/login.htm?returnUrl='+configURL.infoUrl+'/page/#/ZLactive?uuid=' + this.uuid;
	    		} 
			},
			getIndex(userid){
				if(userid==this.userid){
					this.$router.push({path:'/personalIndex',query:{id:userid}})
				}else{
					this.$router.push({path:'/columnsInfo',query:{id:userid}})
				}
				
			},
			initData(){
				var param = {
					'infoUuid':this.infoUuid
				}
				articleDetails(param).then(res=>{
					
					this.info=res.datas
					document.title= this.info.title+"原油资讯_石油资讯_买原油网";
				})
				
			},
			hotArticleFn(){
				hotArticle().then(res=>{
					this.articleList = res.datas;
				})
			},
			getArticle(uuid){
				this.$router.push({path:'/ZLactive',query:{uuid:uuid}})
				this.$router.go(0)
				
			},
			commonListFn(){
				var param={
					infoUuid:this.infoUuid,
					pageNum:this.pageNum,
					pageSize:4
				}
				commonList(param).then(res=>{
					
					this.commonList=res.datas;
					this.total= res.total;	
				})
			},
			pageFn(str){
				this.pageNum = str;
				var param={
					infoUuid:this.infoUuid,
					pageNum:str,
					pageSize:4
				}
				commonList(param).then(res=>{
					this.commonList=res.datas;
				})
			}

			
			
		},
		mounted(){
			// document.location.reload()
			var a =0;
			$(document).ready(function(){
				if($('.share_ws').length>1){
					document.location.reload()
				a++
				}
				
			})
			this.uuid=this.$route.query.uuid;
			var url =window.location.href;
			//var url = configURL.infoUrl+this.uuid
			let _this = this;
			var d= new Date().getTime()
			this.$nextTick(() => {
            window._bd_share_config = {
	                common : {
	                    bdText : _this.art.title,
	                    bdDesc : _this.art.abstract,
						bdPic : "http://www.jubao56.com/upload_admin/2016-07-05%2010:08:50-banner4_m.jpg",
						bdSign:'off',
						bdUrl:url,
						onAfterClick:function(cmd){
						
							var param={
								infoUUId:_this.uuid,
								sharePlatform:cmd,
							}
							share(param).then(res=>{
								console.log(res.message)
							})
						}
	                },
	                share : [{
	                    tag:"share_0",
	                    bdSize : 24
	                }],
	            };
	            const s = document.createElement('script');
				s.type = 'text/javascript';
				s.className= 'share_ws'
	            s.src = 'http://bdimg.share.baidu.com/static/api/js/share.js?v=89860593.js?cdnversion=' + ~(-new Date() / 36e5);
	            document.body.appendChild(s);
	        })
			this.hotArticleFn();
			this.initData();
			this.commonListFn();
			$(document).scroll(function(){
				if($('#rightArticle').length>0){
					var scrollTop = $(document).scrollTop();
					var articleTop = $('.zx-hyzl-dl')[0].offsetTop;
				
					var leftWidth = $("#rightArticle").offset().left;
					if(scrollTop>=articleTop){
						$('#rightArticle').addClass("menu_fix").css("left",leftWidth);
				
					}else{
						$('#rightArticle').removeClass("menu_fix").css("left",0);
					}
				}
			});
		},
		created(){
			this.infoUuid=this.$route.query.uuid
			userLoad().then((res)=>{
				if(res.status==0){
					this.userid=res.datas.memberId
				}
			})
		}
		
	}
</script>

<style>
	.bdsharebuttonbox {
		left: 56px;
	    top: -28px;
	}
	.zx-hyzl-dl {
	    
	    min-height: 400px;
	}
</style>


